import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private http: HttpClient) { }

  getProfileDetails(bNumber: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Person/${bNumber}`);
  }
  getDataRequesDetailsById(surveyId: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/${surveyId}`)
  }
  updateProfileDetails(params: any): Observable<any> {
    return this.http.put(`${environment.BlueNumberGlobalAPI}/api/v1/Person`, params);
  }
  updateProfileStatus(): Observable<any> {
    return this.http.put(`${environment.BlueNumberGlobalAPI}/api/v1/Person/complete`, null);
  }

  uploadFileToSurvey(dataRequestId: any, params: any) {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/File/${dataRequestId}`, params);
  }

  setConnectionRequestDetailsByCode(params: any): Observable<any> {
    return this.http.post(`${environment.BlueNumberGlobalAPI}/api/v1/ConnectionRequestCode/ConnectionByCode`, params);
  }

  verifyOTP(params: any) {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/OtpTracker/VerifyOTPUpdate`, params);
  }

  generateOTP(params: any) {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/OtpTracker/GenerateOTP`, params);
  }

  isBlueNoQualified(params: any) {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataQualified/bluenumberids?ContextId=${params['ContextId']}&PhonenumbersorBluenumbers=${params['blueNo']}`);
  }
  checkSSICredentials(): Observable<any> {
    return this.http.get(`${environment.BlueNumberCredentialsAPI}/api/v1/Account/CheckSSICredential`);
  }
}
