import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SurveyService {

  constructor(private http: HttpClient) { }

  getSurveyDetailsbyQRCode(qrCode: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/QRCode/${qrCode}`);
  }
  getSurveyDetailsResponseForAnsweredQRCode(qrCode: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/ResponseOnly/QRCode/${qrCode}`);
  }
  getSurveyGroupByQRCode(qrCode: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/SurveyGroupStateBySS/QRCode/${qrCode}`);
  }

  getSurveyResponseCount(qrCode: any, registrationSurveyId: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/SurveyGroupResponseCount/QRCode/${qrCode}/${registrationSurveyId}`);
  }
  getDataRequesDetailsById(surveyId: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequest/${surveyId}`)
  }
  saveSurveyResponse(params: any): Observable<any> {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/DataRequestResponse`, params)
  }
  checkSurveyIsValidOrNot(rid: any, qid: any, sid: any): Observable<any> {
   //return this.http.get(`https://bluenumber.global/mm_process/api/check_qualification.php?api_key=bluenumbermmtestapi2023&r_id=${rid}&q_id=${qid}&s_id=${sid}&env=${environment.envType}`);
   return this.http.get(`${environment.BluenumberSurveyOTPValidationUrl}/api/check_qualification.php?api_key=bluenumbermmtestapi2023&r_id=${rid}&q_id=${qid}&s_id=${sid}&env=${environment.envType}`);
  }
  
  getConnectionRequestDetailsByCode(connectionCode: any): Observable<any> {
    return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/ConnectionRequestCode/Bluenumber/${connectionCode}`);
  }
}
